import Helper from '@/function.js';
export default {
	data() {
		return {
			token:"",
			balance:0,
			list:[],
			moneyQuota:0
		}
	},
	onLoad:function(){
		this.token = uni.getStorageSync('token');
		this.getData();
		
	},
	onShow:function(){
		this.getList();
	},

	methods: {
		getData() {
			var _this = this;
			uni.showLoading({
				title: "请稍候..."
			})
			uni.request({
				url: _this.ApiGateWay + '/sale/Member/GetTodayData',
				data: "",
				method: 'POST',
				dataType: 'json',
				header: {
					Authorization: _this.token
				},
				success: function(res) {									
					if (Helper.CheckData(res)) {
						_this.balance = res.data.data.money;
						_this.moneyQuota = res.data.data.moneyQuota;
					} else {
		
					}
				},
				fail: function() {
					uni.hideLoading();
				},
				complete: function() {
					uni.hideLoading();
				}
		
		
			})
		},
		getList() {
			var _this = this;
			uni.showLoading({
				title: "请稍候..."
			})
			uni.request({
				url: _this.ApiGateWay + '/sale/Member/MemberGetBankAccountList',
				data: {
					pageIndex:1,
					pageSize:100,
				},
				method: 'POST',
				dataType: 'json',
				header: {
					Authorization: _this.token
				},
				success: function(res) {
					console.log(res);
		
					if (Helper.CheckData(res)) {
						_this.list = res.data.list
					} else {
		
					}
				},
				fail: function() {
					uni.hideLoading();
				},
				complete: function() {
					uni.hideLoading();
				}
		
		
			})
		},
		capital() {
			uni.navigateTo({
				url: '/pages/my/capital'
			})

		},
		record() {
			uni.navigateTo({
				url: '/pages/my/record'
			})

		},
		bank() {
			uni.navigateBack({
				delta:1
			})
		},
		addback() {
			uni.navigateTo({
				url: '/pages/my/bank'
			})
		},
		withdrawals() {
			if (this.list.length==0)
			{
				uni.showModal({
					title:"出错了",
					content:"请先添加提现银行卡",
					showCancel:false,								
				})	
				return;
			}
			
			uni.navigateTo({
				url: '/pages/my/Withdrawals'
			})
		}





	}

}
